import Vue from 'vue'
import Router from 'vue-router'
import RtaHistory from '@/components/RtaHistory'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/*',
      name: 'RtaHistory',
      component: RtaHistory
    }
  ]
})



